code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header {
  position: relative;
  width: 100vw;
  height: 35vh;
  margin-bottom: 2em;
  border-bottom: 5em;
  background-color: #13408b;
}

h1 h4 {
  text-align: center;
  padding-bottom: 3em;
  padding-top: 0.5em;
  color: goldenrod;
}

img {
  display: flex;
  justify-content: flex-start;
  height: 15vh;
  padding-top: 1em;
}

.app-button {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  letter-spacing: 0.4em;
  font-size: 30px;
  padding: 1em;
  width: 30%;
  min-width: 340px;
  text-align: center;
  text-decoration: none;
  font-family: "Poiret One" !important;
  color: goldenrod;
  background: transparent;
  position: relative;
  transition: 0.5s;
  align-items: center;
}

.app-button:before,
.app-button:after {
  border: 4px solid;
}

.app-button:before,
.app-button:after {
  content: "";
  position: absolute;
}

.app-button:before {
  top: -15px;
  right: -2px;
  bottom: -15px;
  left: -2px;
}

.app-button:after {
  top: -2px;
  right: -15px;
  bottom: -2px;
  left: -15px;
}

.app-button:hover {
  color: white;
  background-color: goldenrod;
}

.app-button:active {
  color: white;
  background-color: goldenrod;
}

.app-button:focus {
  color: white;
  background-color: goldenrod;
}

.buttonContainer {
  display: flex;
  justify-content: center;
}


.option {
    border: none
}

.correct{
    background-color: green;
}

.incorrect{
    background-color: red;
}

.selected{
    border:solid gold
}
.slider {
  margin-top: 20px;
  margin-bottom: 20px;
  max-width: 100vw;
  background-color: white;
}

ul::before li::before button::before {
  font-size: 10px;
  color: rgb(150, 158 171);
}
li.slick-active button::before {
  color: white;
}
.slick-list {
  overflow: hidden;
}
button {
  margin: 5vw;
  z-index: 1;
}

p {
  border: 4px solid transparent;
  border-radius: 4px;
  width: 100%;
  height: 100%;
  box-shadow: rgb(0 0 0 /69%) 0px 26px 30px -10px,
    rgb(0 0 0 /73%) 0px 16px 10px -10px;
  transition-duration: 300ms;
}

.slick-next,
.slick-prev {
  background: transparent;
}

h3 {
  text-align: center;
  color: white;
}

.container {
  background: url(https://img.rawpixel.com/s3fs-private/rawpixel_images/website_content/v945-tang-frame-03-b_1.jpg?w=800&dpr=1&fit=default&crop=default&q=65&vib=3&con=3&usm=15&bg=F4F4F3&ixlib=js-2.2.1&s=ccb08550a9ad540698a77b3b8931ab84);
  
  /* Full height */
  height: 100%; 
  width: auto;

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}


.flex-container {
  display: flex;
  flex-flow: column nowrap;
  width: 100vw;
}

